.about-me-button { 
    -webkit-transition: .5s ease; 
    transition: .5s ease;
    position: absolute;
    left: 45%;
    top: 47%;
    background-color: #FFFFFF;
    padding: 16px 32px;
    -webkit-transform: translate(-0%, -0%);
            transform: translate(-0%, -0%);
    -ms-transform: translate(-0%, -0%);
    opacity: 0; 
    border-radius: 25px;
}

